









































































































	import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
	import expData from '../../../common/exp.data.js';

	export default {
		components: {
			mpvuePicker
		},
		data() {
			return {
				credit_card: {
					line_credit: '',
					credit_code: '',
					exp_date: '',
					safe_code:'',
					bill_time:'',
					repay_time:'',
					phone:'',
					bank_name:'',
					bank_id:'',
					openid:'',
					sessionKey:'',
				},
				bannerShow: false,
				tip_img: '',
				loading: false,
				pickerSingleTwoArray: expData,
				themeColor: '#007AFF',
				mode: '',
				deepLength: 1,
				pickerValueDefault: [0],
				pickerValueArray: [],
				pickerSingleArray:[],
			}
		},
		onLoad() {this.bankInfo();},
		onShow() {},
		onShareAppMessage() {
			return {
				title: this.webshareTitle,
				path: '/pages/tabBar/index/index'
			}
		},
		computed: {},
		methods: {
			addCredit(){
				this.credit_card.openid = this.$store.state.openid;
				this.credit_card.sessionKey = this.$store.state.sessionKey;
				const that = this;
				if(this.credit_card.credit_code==''){
					uni.showModal({
						content: '请输入信用卡号',
						showCancel: false
					});
					return;
				}
				
				if(this.credit_card.bank_name==''){
					uni.showModal({
						content: '请选择银行',
						showCancel: false
					});
					return;
				}
				
				if(this.credit_card.line_credit==''){
					uni.showModal({
						content: '请输入信用卡额度',
						showCancel: false
					});
					return;
				}
				
				if(this.credit_card.exp_date==''){
					uni.showModal({
						content: '请输入有效日期',
						showCancel: false
					});
					return;
				}
				
				if(this.credit_card.safe_code==''){
					uni.showModal({
						content: '请输入CVV2码',
						showCancel: false
					});
					return;
				}
				
// 				if(this.credit_card.bill_time==''){
// 					uni.showModal({
// 						content: '请输入账单日',
// 						showCancel: false
// 					});
// 					return; 
// 				}
// 				
// 				if(this.credit_card.repay_time==''){
// 					uni.showModal({
// 						content: '请输入还款日',
// 						showCancel: false
// 					});
// 					return;
// 				}
				
				if(this.credit_card.phone==''){
					uni.showModal({
						content: '请输入银行预留手机号',
						showCancel: false
					});
					return;
				}
				
				
				
				try {
					if(this.loading){
						return;
					}
					this.loading = true;
					
					this.$http.post(this.websiteUrl + '/api/card/addCredit', this.credit_card, (res) => {
						if(res.data.code==1){
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
									uni.navigateBack();
								}
							})
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}
						
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false,
						success:function() {
							that.loading = false;
						}
					});
				}
			},
			bankInfo(){
				try {
					this.$http.post(this.websiteUrl + '/api/card/bankInfo', {
					}, (res) => {
						if (res.data.code == 1) {
							this.pickerSingleArray = res.data.result;
						}
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false
					})
				}
			},
			closeBanner: function() {
				this.bannerShow = false;
			},
			showBanner: function(e) {
				this.tip_img = e;
				this.bannerShow = true;
			},
			onCancel(e) {
				console.log(e)
			},
			// 单列
			showSinglePicker() {
				this.pickerValueArray = this.pickerSingleArray
				this.mode = 'selector'
				this.deepLength = 1
				this.pickerValueDefault = [0]
				this.$refs.mpvuePicker.show()
			},
			// 多列
			showSingleTwoPicker() {
				this.pickerValueArray = this.pickerSingleTwoArray
				this.mode = 'multiSelector'
				this.deepLength = 2
				this.pickerValueDefault = [0, 0]
				this.$refs.mpvuePicker.show()
			},
			onConfirm(e) {
				if(e.value.length == 2){
						this.credit_card.exp_date = e.value[0] + '' + e.value[1];
				}else if(e.value.length == 1){
						this.credit_card.bank_id = e.value[0];
					  this.credit_card.bank_name = e.label;
				}	
			},

		},
		onBackPress() {
			if (this.$refs.mpvuePicker.showPicker) {
				this.$refs.mpvuePicker.pickerCancel();
				return true;
			}

			if (this.bannerShow) {
				this.bannerShow = false;
				return true;
			}
		},
		onUnload() {
			if (this.$refs.mpvuePicker.showPicker) {
				this.$refs.mpvuePicker.pickerCancel()
			}
		}
	}
